import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
import { post,tokenpost} from '@/api/user.js';
import { api } from '@/config/common.js';
import cuLoading from '@/components/custom/cu-loading.vue';
import WucTab from '@/components/wuc-tab/wuc-tab.vue';
import helangCheckbox from "@/components/helang-checkbox/helang-checkbox.vue"
export default {
	components: {
		uniLoadMore,
		uniList,
		uniListItem,
		WucTab,
		"helang-checkbox":helangCheckbox
	},
	data() {
		return {
			loadmore:'more',
			pageIndex: 0,
			pageRows: 15,
			title: '续费/订单',
			pickerIndex:2,
			TabCur: 0,
			content_show_id:0,
			dataList: [],
			tabList: [{ name: '续费' }, { name: '我的订单' }],
			OrderStatusList:['待支付','已支付'],
			PayTypeList:{
				'0': '',
				'1': '银行账号',
				'2': '微信',
				'3': '支付宝',
				'4': '现金'
			},
			productList: []
		};
	},
	onLoad(){
		// this.$refs.checkbox.set({
		// 	type:'radio',		// 类型：复选框
		// 	column:2,				// 分列：3
		// 	list:this.list	// 列表数据
		// });
		this.pickerIndex = 3;
		// //this.loadData();
		this.loadProduct();
	},
	onShow(){
	},
	mounted(){
	},
	methods: {
		handleRefreshPage() {
			console.log('refreshpage');
		},
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		tabChange(val) {
			console.log(val);
			switch (val){
				case 0:
					this.content_show_id = 0
					
					this.$refs.checkbox.set({
						type:'radio',		// 类型：复选框
						column:2,				// 分列：3
						list:this.list	// 列表数据
					});
					this.pickerIndex = 3;
					break;
				case 1:
					this.content_show_id =1
					break;
				default:
					this.content_show_id =0
				break;
			}
		},
		
		handleSubmit() {
			let data = this.$refs.checkbox.get();	// 组件返回的数据
		    console.log("发起支付:"+data.value);
		    this.loading = true;
		    uni.login({
		        success: (e) => {
		            console.log("login success", e);
		            uni.request({
		                url: `https://unidemo.dcloud.net.cn/payment/wx/mp?code=${e.code}&amount=${data.value}`,
		                success: (res) => {
		                    console.log("pay request success", res);
		                    if (res.statusCode !== 200) {
		                        uni.showModal({
		                            content: "支付失败，请重试！",
		                            showCancel: false
		                        });
		                        return;
		                    }
		                    if (res.data.ret === 0) {
		                        console.log("得到接口prepay_id", res.data.payment);
		                        let paymentData = res.data.payment;
		                        uni.requestPayment({
		                            timeStamp: paymentData.timeStamp,
		                            nonceStr: paymentData.nonceStr,
		                            package: paymentData.package,
		                            signType: 'MD5',
		                            paySign: paymentData.paySign,
		                            success: (res) => {
		                                uni.showToast({
		                                    title: "感谢您的赞助!"
		                                })
		                            },
		                            fail: (res) => {
		                                uni.showModal({
		                                    content: "支付失败,原因为: " + res
		                                        .errMsg,
		                                    showCancel: false
		                                })
		                            },
		                            complete: () => {
		                                this.loading = false;
		                            }
		                        })
		                    } else {
		                        uni.showModal({
		                            content: res.data.desc,
		                            showCancel: false
		                        })
		                    }
		                },
		                fail: (e) => {
		                    console.log("fail", e);
		                    this.loading = false;
		                    uni.showModal({
		                        content: "支付失败,原因为: " + e.errMsg,
		                        showCancel: false
		                    })
		                }
		            })
		        },
		        fail: (e) => {
		            console.log("fail", e);
		            this.loading = false;
		            uni.showModal({
		                content: "支付失败,原因为: " + e.errMsg,
		                showCancel: false
		            })
		        }
		    })
		},
		loadProduct(){
			this.$refs.loading.open();
			const senddata = {
				pageIndex: 1,
				pageRows: -1
			};
			tokenpost(api.GetProductList,senddata).then(res => {
				this.$refs.loading.close();
				if (res.status == 200 && res.data.returnCode == '0000') {
					this.productList = res.data.data.resultList;
					console.log(this.productList);
				} else {
					this.$api.msg(res.data.returnMessage);
				}
			})
			.catch(error => {
				this.loadmore = 'more',
				this.$refs.loading.close();
				this.$api.msg('请求失败fail');
			});
		},
		loadData(){
			this.loadmore = 'loading';
			this.$refs.loading.open();
			const senddata = {
				pageIndex: this.pageIndex+1,
				pageRows: this.pageRows
			};
			tokenpost(api.GetOrderList,senddata).then(res => {
				this.$refs.loading.close();
				if (res.status == 200 && res.data.returnCode == '0000') {
					if(res.data.data.resultList.length ===0){
						this.loadmore = "noMore"
						return;
					}else{
						this.dataList =this.dataList.concat(res.data.data.resultList);
						this.pageIndex = this.pageIndex+1 ;
						this.loadmore = "more"
					}

				} else {
					this.loadmore = 'more',
					this.$api.msg(res.data.returnMessage);
				}
			})
			.catch(error => {
				this.loadmore = 'more',
				this.$refs.loading.close();
				this.$api.msg('请求失败fail');
			});
		}
	}
};
